.zoneManagerWrapper {
	width: 100%;
	height: min(240px, 25vh);
	background: white;
	border: 2px solid black;
	z-index: 1000;
	overflow: auto;
	display: grid;
	grid-auto-rows: 50px;
}
.addRowButton {
	max-width: 100%;
}
.parseZoneButton {
	max-width: 200px;
}
.zoneManagerRow {
	display: flex;
	color: black;
	background: #fafafa;
	border-bottom: 1px solid black;
	min-width: fit-content;
	flex-grow: 1;
}
.zoneManagerRow .deleteButton {
	margin-left: auto;
}
.geometrySelect {
	min-width: 12ch;
}
.operationSelect img {
	max-height: 20px;
	margin-bottom: -5px;
}
.geometrySelect,
.operationSelect {
	padding-inline: 5px;
	margin-bottom: -1px;
}
